@use 'base/variables' as v;
@use 'base/mixins' as m;

.table {
    width: 100%;
    border-collapse:collapse ;
    
    &__thead {
        background-color: v.$gris-oscuro;
        color: v.$blanco;
    }

    &___th {
        padding: 1rem;
        text-align: center;
    }

    &__tr {
        &:nth-child(even) {
            background-color: darken(v.$gris-claro, 5%);
        }
    }
    
    &__td {
        padding: 1.5rem 1rem;

        &--acciones {
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            gap: 2rem;
            align-items: center;
        }
    }

    &__accion {
        all: unset;
        text-transform: uppercase;
        font-size: 1.4rem;
        font-weight: v.$bold;
        
            &--editar {
                color: v.$azul;
        
            }
        
            &--eliminar {
                color: v.$rojo;
            }
        
            &:hover {
                cursor: pointer;
            }
    }

}